.header {
  align-items: center;
  align-self: stretch;
  background-color: var(--white);
  border: 1px none;
  display: flex;
  flex-direction: column;
  gap: 8px;
  justify-content: center;
  padding: 24px 120px;
}

.container-4 {
  align-items: center;
  border: 1px none;
  display: flex;
  justify-content: space-between;
  width: 1200px;
}

.oline-sign-logo {
  height: 40px;
  min-width: 246.67px;
}

.buttons {
  align-items: flex-start;
  border: 1px none;
  display: flex;
  gap: 16px;
  position: relative;
  width: fit-content;
}

