:root { 
  --black: #000000;
  --blue: #2a71f8;
  --bright-turquoise: #18eff2;
  --cornflower-blue: #5993ff;
  --cultured-pearl: #f2f5f7;
  --shark: #262626;
  --shark-2: #262626b2;
  --shark-3: #2626268c;
  --white: #ffffff;
 
  --font-size-l: 22px;
  --font-size-m: 19px;
  --font-size-s: 16px;
  --font-size-xl: 24px;
  --font-size-xxl: 28px;
  --font-size-xxxl: 34px;
  --font-size-xxxxl: 48px;
 
  --font-family-sb_sans_display-semibold: "SB Sans Display-Semibold", Helvetica;
  --font-family-sb_sans_text-medium: "SB Sans Text-Medium", Helvetica;
  --font-family-sb_sans_text-regular: "SB Sans Text-Regular", Helvetica;
  --font-family-sf_pro_display-semibold: "SF Pro Display-Semibold", Helvetica;
}
.sbsanstext-regular-normal-shark-19px {
